import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { likeVideo } from "../../actions/videoActions";
import IState from "../../interfaces";
import  "./Like.css"
const Like:React.FC<any> = ({ isLiked, video }) => {
  const [state, setState] = useState(isLiked)
  const dispatch = useDispatch();
  const { nowPlaying } = useSelector((state:IState) => state.video);

  const handleonClick = React.useCallback(() => {
    setState((prev:boolean) =>(!prev));
    likeVideo(video, nowPlaying)(dispatch);
  },[dispatch,video,nowPlaying]);

  return (
    <div className={"col panel-icon-like " + (state ? "liked" : "")}>
      <i className="panel-icons fas fa-heart" onClick={handleonClick} />
    </div>
  );

}
export default Like;