import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { loginUser } from "../../actions/authActions";
import Input from "../common/Input";
import "./Auth.css";
import ErrorPopup from "../common/ErrorPopup";
import IState from '../../interfaces'
import { popErrors } from "../../actions/errorActions";
const Login  = ({history}:any) => {

  const dispatch = useDispatch();
  const {errors} = useSelector((state:IState) => state)
  const [loginForm, setLoginForm] = useState({
    email:"",
    password:"",
  })
  const handleonChange = ((e:any) => {
    const {name,value} = e.target;
    setLoginForm(prev=>({
      ...prev,
      [name]:value
    }));
  });

  const handleonSubmit = React.useCallback((e) => {
    e.preventDefault();
    loginUser(loginForm, history)(dispatch);
  },[loginForm,dispatch,history]);

  React.useEffect(() =>{
    dispatch(popErrors());
  },[dispatch])

    return (
      <div id="login" className="auth-form">
        <div className="p-3 mb-2 border rounded shadow border-grey ">
          {" "}
          <h3>Giriş</h3>
          <form onSubmit={handleonSubmit}>
            <Input
              name="email"
              placeholder="Email"
              errors={errors.email}
              onChange={handleonChange}
            />
            <Input
              name="password"
              placeholder="Password"
              type="password"
              errors={errors.password}
              onChange={handleonChange}
            />
            <input type="submit" className="btn btn-success btn-block mt-4" />
          </form>
          
          <ErrorPopup />
        </div>
      </div>
    );
  
}

export default Login;