import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { registerUser } from "../../actions/authActions";

import Input from "../common/Input";
import ErrorPopup from "../common/ErrorPopup";
import "./Auth.css";
import IState from "../../interfaces";
import { popErrors } from "../../actions/errorActions";

const Register = ({ history }: any) => {
  const dispatch = useDispatch();
  const { errors } = useSelector((state: IState) => state)
  const [registerForm, setRegisterForm] = useState({
    username: "",
    email: "",
    gender: "",
    image: "",
    password: "",
    password2: ""
  })

  const handleonChange = (e: any) => {
    const { name, value } = e.target;
    setRegisterForm(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleonSubmit = React.useCallback(async (e) => {
    e.preventDefault();
    registerUser(registerForm, history)(dispatch);
  }, [registerForm, dispatch, history]);

  React.useEffect(() => {
    dispatch(popErrors());
  }, [dispatch])


  return (
    <div id="register" className="auth-form">
      <div className="p-3 mb-2 border rounded shadow border-grey ">
        <h3>Qeydiyyat</h3>
        <form onSubmit={handleonSubmit}>
          <Input
            name="username"
            placeholder="Username"
            errors={errors.username}
            onChange={handleonChange}
          />
          <Input
            name="email"
            placeholder="Email"
            errors={errors.email}
            onChange={handleonChange}
          />
          <div className="form-group">
            <select className="form-control" name="gender" id="gender" defaultValue="default" onChange={handleonChange} required>
              <option value="default" disabled>Cinsinizi Seçin...</option>
              <option value="male">Kişi</option>
              <option value="female">Qadın</option>

            </select>
          </div>

          <Input
            name="password"
            placeholder="Password"
            type="password"
            errors={errors.password}
            onChange={handleonChange}
          />
          <Input
            name="password2"
            placeholder="Repeat password"
            type="password"
            errors={errors.password2}
            onChange={handleonChange}
          />
          <input type="submit" className="btn btn-success btn-block mt-4" />
        </form>
        <ErrorPopup />
      </div>
    </div>
  );
}

export default Register;

