import { EACTION_TYPES, IAuthAction } from "../actions/types";

const initialState = {
  isAuthenticated: false,
  user: {}
};

const authReducer = (state = initialState, action:IAuthAction)=> {
  switch (action.type) {
    case EACTION_TYPES.SET_CURRENT_USER:
      return {
        ...state,
        isAuthenticated: Boolean(Object.keys(action.payload).length),
        user: action.payload
      };
    default:
      return state;
  }
}

export default authReducer;
