import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { logoutUser } from "../../actions/authActions";
import ReactTooltip from 'react-tooltip';
import "./Navbar.css";

import SearchBar from "../feed/SearchBar";
import IState from "../../interfaces";
import isToday from "../../service/isToday";
import finishTime from "../../service/finishTime";

const Navbar = () => {
  const { push } = useHistory();
  const { auth } = useSelector((state: IState) => state);
  const { videoList } = useSelector((state: IState) => state.video)
  const [time, setTime] = useState("");
  const dispatch = useDispatch();
  const handleOnLogout = (e: any) => {
    e.preventDefault();
    logoutUser()(dispatch);
    push("/");
  };

  const userVideosList = videoList.filter(v => v.user._id === auth.user.id);
  const todayVideoCnt = userVideosList.reduce((acc, video): any => {
    const currVideoDate = new Date(video.date);
    if (isToday(currVideoDate)) {
      return acc + 1;
    } else {
      return acc;
    }
  }, 0)
  React.useEffect(() => {
    setInterval(() => setTime(finishTime), 1000)
  }, [])
  return (
    <nav className="navbar navbar-fixed-top mb-4 navbar-expand-sm navbar-dark ">
      <div className="container">
        <Link className="navbar-brand" to="/">
          {/* <i className="fab fa-youtube" />
          &nbsp; #ZövqBurada */}
          <div style={{width:"135px"}}>
          <svg viewBox="31 165 500 170" version="1.1" id="svg_null"><g id="root" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd"><rect id="background" fill="none" x="0" y="0" width="200" height="88"></rect><circle id="shape.accent" fill="#28a745" cx="125" cy="285" r="50"></circle><circle id="shape.secondary" fill="#aea1ea" cx="85" cy="235" r="40"></circle><circle id="shape.primary" fill="#8c54a1" cx="130" cy="195" r="30"></circle><g id="Group" transform="translate(101.000000, 230.000000)"><rect id="Rectangle-4" x="0" y="0" width="354" height="74"></rect><text id="headerText" fontFamily="monospace" fontSize="53.151906158357775" fontWeight="400" line-spacing="74" fill="#fff" data-text-alignment="L" fontStyle="normal" letterSpacing="NaN"><tspan x="67" y="50">#ZövqBurada</tspan></text></g></g></svg>

          </div>
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#mobile-nav"
        >
          <span className="navbar-toggler-icon" />
        </button>

        <div className="collapse navbar-collapse" id="mobile-nav">
          <ul className="navbar-nav ml-auto">
            {todayVideoCnt < 2 ? (<li className="nav-item">
              <SearchBar />
            </li>) : (<li className="nav-item wait-txt">
              <span data-tip data-for='finishTime'>
                <span className="finishTime">{`    ${time}`}</span>
              </span>
              <ReactTooltip id='finishTime' type='warning' effect='solid'>
                <span style={{color: 'black'}}>Sayta gündəlik video yükləmə limiti 2(iki)-dir. Növbəti yükləmə üçün {`${time.split(":")[0]} saat ${time.split(":")[1]} dəqiqə ${time.split(":")[2]} saniyə`} gözləyin...</span>
              </ReactTooltip>
            </li>)}


            <li
              className="nav-item my-2"
              data-toggle="collapse"
              data-target=".navbar-collapse.show"
            >
              <Link className="nav-link" to="/leaderboard">
                TOP Liderlər
              </Link>
            </li>
            {auth.isAuthenticated ? (
              <>
                <li
                  className="nav-item my-2"
                  data-toggle="collapse"
                  data-target=".navbar-collapse.show"
                >
                  <Link className="nav-link text-primary" to="/chat">
                    Söhbət
                  </Link>
                </li>
                <li
                  className="nav-item my-2"
                  data-toggle="collapse"
                  data-target=".navbar-collapse.show"
                >
                  <Link className="nav-link mylikes" to="/mylikes">
                    Bəyəndiklərim
                  </Link>
                </li>
                <li
                  className="nav-item my-2"
                  data-toggle="collapse"
                  data-target=".navbar-collapse.show"
                >
                  <Link
                    to={"/u/" + auth.user.username}
                    className="nav-link my-profile"
                  >
                    <strong>{auth.user.username}</strong>
                  </Link>
                </li>
                <li
                  className="nav-item my-2 "
                  data-toggle="collapse"
                  data-target=".navbar-collapse.show"
                  onClick={handleOnLogout}
                >
                  <div className="nav-link logout">Çıxış</div>
                </li>
              </>
            ) : (
              <>
                <li
                  className="nav-item my-2"
                  data-toggle="collapse"
                  data-target=".navbar-collapse.show"
                >
                  <Link className="nav-link" to="/register">
                    Qeydiyyat
                  </Link>
                </li>
                <li
                  className="nav-item my-2"
                  data-toggle="collapse"
                  data-target=".navbar-collapse.show"
                >
                  <Link className="nav-link" to="/login">
                    Giriş
                  </Link>
                </li>
              </>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
