import {EACTION_TYPES, IErrorAction } from "../actions/types";

const initialState = {};

const errorReducer = (state = initialState, action:IErrorAction)=> {
  switch (action.type) {
    case EACTION_TYPES.GET_ERRORS:
      return action.payload;
    case EACTION_TYPES.CLEAR_ERRORS:
      return {};
    default:
      return state;
  }
}

export default errorReducer;
