import React from "react";
import { useDispatch, useSelector } from "react-redux";
import swal from "sweetalert";
import { deleteVideo } from "../../actions/videoActions";
import IState from "../../interfaces";
import "./Trash.css";

const Trash:React.FC<any> = ({ video }) => {
  
  const { nowPlaying } = useSelector((state: IState) => state.video);
  const dispatch = useDispatch();
  const handleOnClick = React.useCallback((e) => {
    swal({
      title: "Əminsinizmi?",
      text: "Videonuzu sildikdən sonra videoa aid bütün layklar silinəcək.",
      icon: "warning",
      buttons: [true],
      dangerMode: true,
    })
      .then((willDelete) => {
        if (willDelete) {
          swal("Sizin Videonuz Bazadan Silindi.", {
            icon: "success",
          });
          deleteVideo(
            video,
            nowPlaying
          )(dispatch);
        }
      });
  }, [video, nowPlaying, dispatch]);
  return (
    <div className="col trash">
      <i className="panel-icons fas fa-trash" onClick={handleOnClick} />
    </div>
  );

}

export default Trash;
