import { TextField } from '@material-ui/core';
import React, { useEffect, useRef, useState } from "react"
import { useSelector } from 'react-redux';
import io from "socket.io-client";
import IState from '../../interfaces';
import stc from 'string-to-color';
import "./Chat.css"
import swal from 'sweetalert';
import {addMessage, getMessages} from '../../actions/chatActions';

interface ISocket {
	name: "",
	message: ""
}
const Chat: React.FC = () => {
	const [state, setState] = useState({ message: "", name: "" })
	const [chat, setChat] = useState<ISocket[]>([]);
	const { auth } = useSelector((state: IState) => state);
	const { username } = auth.user;
	const socketRef: any = useRef()

	useEffect(
		() => {
			if (auth.isAuthenticated) {
				socketRef.current = io("http://localhost:8000");
				socketRef.current.on("message", ({ name, message }: any) => {
					setChat([...chat, { name, message }]);
					const renderSection = document.querySelector(".render-chat");
					if (renderSection) renderSection.scrollTop = renderSection?.scrollHeight;
				})
				return () => socketRef.current.disconnect()
			}

		},
		[auth.isAuthenticated, chat]
	)

	const onTextChange = (e: any) => {
		setState({ ...state, [e.target.name]: e.target.value })
	}
	const replyToUser = (username: string) => {
		setState(prev => ({
			...prev,
			message: `${username},`
		}));
		document.getElementById("message")?.focus();
	}
	const onMessageSubmit = (e: any) => {
		const { message } = state;
		if (message.length > 165) {
			e.preventDefault();
			swal("Simvol Sayı Maksimum 165 olmalıdır");
			return;
		}
		const chatmessage = {
			username,
			text: message
		}
		addMessage(chatmessage);
		socketRef.current.emit("message", { name: username, message })
		e.preventDefault();
		setState({ message: "", name: username });
	}
	useEffect(() => {
		getMessages()
		.then(messages=>{
			messages.forEach((m:any)=>{
				setChat(prev => ([
					...prev,
					{
						name: m.username,
						message: m.text
					}
				]))
			})
		})			
	}, [])


	const renderChat = () => {

		return chat.map(({ name, message }, index) => (


			<div className="chat-text-field" style={auth.user.username === name ? { marginLeft: "auto" } : {} && message.includes(auth.user.username) ? { backgroundColor: "rgb(12 213 120)" } : {}} key={index}>
				<h6 style={{ cursor: "pointer", color: stc(name) }} onClick={() => replyToUser(name)}>
					{name}
					<br />
					<span className="chat-text">{message}</span>
				</h6>
			</div>

		))
	}

	return (
		<div className="card-c">
			<div className="chat-card">


				<form onSubmit={onMessageSubmit}>
					<div className="text-area">
						<TextField
							inputProps={{ maxLength: 165 }}
							className="message"
							name="message"
							autoFocus={true}
							onChange={(e) => onTextChange(e)}
							value={state.message}
							id="message"
							variant="outlined"
							label="Mesajınız"
							autoComplete="off"
						/>
					</div>
					<i onClick={onMessageSubmit} className="fas fa-paper-plane"></i>
				</form>
				<div className="render-chat mb-3">
					{renderChat()}
				</div>
			</div>
		</div>
	)
}

export default Chat;

