import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { loadLeaderboard } from '../../actions/userActions';
import IState from '../../interfaces';
import "./Leaderboard.css"
const Leaderboard = ()=> {
const dispatch = useDispatch();
  const { user: { leaderboard }, video } = useSelector((state: IState) => state);
      let userlist = leaderboard.map((user, i) => <User key={i} username={ user.username } rank={ i + 1 } img={ user.image } totallike={user.likesCount} />);
  

      React.useEffect(() => {
        loadLeaderboard()(dispatch);
      }, [video, dispatch]);
      return (
        <div className="leaderboard-container">
          <LeaderboardHeader />
          <ColumnHeader/>
          { userlist }
        </div>
      )
    
  
  }
  
  const LeaderboardHeader = () => {
    return (
      <div className="leadheader">
          <h2>TOP-10</h2>
      </div>
    )
  }
  
  const ColumnHeader = () => (
    <div className="row colheader">
        <div className="col-xs-1">
          <h4>#</h4>
        </div>
        <div className="col-xs-3">
          <h4>Avatar</h4>
        </div>
        <div className="col-xs-3 username">
          <h4>İstifadəçi adı</h4>
        </div>
        <div className="col-xs-3 totalLike">
          <h4>Ümumi Bəyənmə Sayı</h4>
        </div>
      </div>
  );

  const User = ({ username,rank,img,totallike  }:any) => {
    return (
    
      <Link style={{textDecoration:"none",color:"unset"}} to={"/u/" + username}>
      <div className="row users  vcenter">
          <div className="col-xs-1 rank">
            <h4>{ rank }</h4>
          </div>
          <div className="col-xs-3 img">
            <img src={ img } alt='avatar' />
          </div>
          <div className="col-xs-3 name">
            <h5>{ username }</h5>
          </div>
          <div className="col-xs-3 text-align-left">
            <span className="">
            <i style={{fontSize:"22px",color:"red"}} className="fas fa-heart">
                      </i>
                      <span className="total text-dark"> {totallike}</span>
                   
                  </span>
          </div>
        </div>
        </Link>
        
    )
  }

  export default Leaderboard;