import axios from "axios";
export const getMessages = () =>{
   return axios.get("./api/message/all")
    .then(({ data }) =>data)
    .catch(err=>console.log(err));
}
interface IChatMessage  {
    username: string;
    text:string;
}
export const addMessage = (chatmessage:IChatMessage) =>{
    axios.post("./api/message/add", chatmessage)
    .then(({data})=>data)
    .catch(err=>console.log(err));
}

