import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { getProfile, deleteUser } from '../../actions/userActions'
import "./Description.css";
import Video from "../common/Video";
import Description from "./Description";
import swal from "sweetalert";
import IState from "../../interfaces";
import Img from "react-cool-img";
import loadingImg from "../../images/loading.gif";

const PublicProfile:React.FC<any> = (props) => {
  const { auth, user: { profile } } = useSelector((state:IState) => state);
  const dispatch = useDispatch();
  const { match: { params: { username } }, history } = props;

  React.useEffect(() => {
    if (username) {
      getProfile(username, history)(dispatch);
    }
  },[dispatch,history,username])
  
  const handleOnDeleteUser = (e:any) => {
    e.preventDefault();
      swal({
        title: "Əminsinizmi?",
        text: "Profilinizi sildikdən sonra geri qaytarmaq mümkün olmayacaq.",
        icon: "warning",
        buttons: [true],
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {
          swal("Sizin Profiliniz Bazadan Silindi.", {
            icon: "success",
          });
          deleteUser(history)(dispatch);
        }
      });
    
      
    
  };

 
  const { isAuthenticated } = auth;
  const personal =
    isAuthenticated && profile.username === auth.user.username;
  return (
    <div className="profile container">
      <div className="row">
      <Img style={{borderRadius:"15px"}} placeholder={loadingImg} width="100" src={profile.image} alt="avatar" />
        <div className="col my-1">
          <h1>{profile.username}</h1>
          <div className="my-1">Verdiyi Bəyənmə sayı: {profile.likesGiven}</div>
          <div className="my-1">Aldığı Bəyənmə sayı: {profile.likesCount}</div>
        </div>
      </div>

      <Description personal={personal} />

      {personal && (
        <div className="container">
          <div className="row">
            <button
              type="button"
              className="btn btn-danger my-4"
              onClick={handleOnDeleteUser}
            >
              Hesabı Sil
            </button>
          </div>
        </div>
      )}

      {profile.uploads && Boolean(profile.uploads.length) && (
        <React.Fragment>
          <h4 className="my-3">Yükləmələr</h4>
          <div className="feed">
            {profile.uploads.map((video:any, index:number) => {
              return <Video key={index} video={video} />;
            })}
          </div>
        </React.Fragment>
      )}
    </div>
  );
}

export default PublicProfile;
