import { EACTION_TYPES, IUserAction } from "../actions/types";

const initialState = {
  leaderboard: [],
  profile: {}
};

const userReducer = (state = initialState, action: IUserAction) => {
  switch (action.type) {
    case EACTION_TYPES.LOAD_LEADERBOARD:
      return {
        ...state,
        leaderboard: action.payload
      };
    case EACTION_TYPES.GET_PROFILE:
      return {
        ...state,
        profile: action.payload
      };
    default:
      return state;
  }
}
export default userReducer;