import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addVideo } from "../../actions/videoActions";
import "./SearchBar.css";
import Input from "../common/Input";
import ErrorPopup from "../common/ErrorPopup";
import IState from "../../interfaces";

const SearchBar: React.FC = () => {
  const dispatch = useDispatch();
  const { video, errors, auth } = useSelector((state: IState) => state);
  const [search, setSearch] = useState({
    input: "",
    loading: false
  })

  React.useEffect(() => {
    setSearch(prev => ({ ...prev, loading: false }))
  }, [video, errors]);

  const handleOnChange = (e:any) => {
    const { value } = e.target;
    setSearch(prev => ({
      ...prev,
      input: value
    }));
  };

  const handleOnSubmit = React.useCallback((e) => {
    e.preventDefault();
    if (!search.loading) {
      setSearch(prev => ({
        ...prev,
        loading: true
      }));
      addVideo({ videoUrl: search.input })(dispatch);
    }
  }, [search, dispatch]);


  return (
    <div className="nav-link">
      <form
        className="form-inline"
        autoComplete="off"
        onSubmit={handleOnSubmit}
      >
        {auth.isAuthenticated && (
          <div className="inner-row">
            <Input
              name="youtube-search"
              placeholder="Youtube linkin bura"
              type="search"
              defaultValue={search.input}
              onChange={handleOnChange}
              errors={errors.video}
              loading={search.loading}
            />
            <button className="btn btn-success mx-1" type="submit">
              {search.loading ?
                <i className="fas fa-copyright spin" /> :
                <i className="fas fa-arrow-circle-right" />
              }
            </button>
          </div>
        )}
      </form>
      <ErrorPopup />
    </div>
  );
}

export default SearchBar;
