import {
  EACTION_TYPES, IVideoAction
} from "../actions/types";

const initialState = {
  videoList: [],
  nowPlaying: {}
};

const videoReducer = (state = initialState, action:IVideoAction) => {
  switch (action.type) {
    case EACTION_TYPES.LIST_VIDEOS:
      return { ...state, videoList: [...action.payload] };
    case EACTION_TYPES.PLAY_VIDEO:
      return { ...state, nowPlaying: action.payload };
    case EACTION_TYPES.CLOSE_VIDEO:
      return { ...state, nowPlaying: {} };
    case EACTION_TYPES.UPDATE_NOW_PLAYING:
      return { ...state, nowPlaying: action.payload };
    default:
      return state;
  }
}

export default videoReducer;