import axios from "axios";
import {EACTION_TYPES } from "./types";
import { logoutUser } from "./authActions";
import { Dispatch } from "redux";

export const loadLeaderboard = () => (dispatch:Dispatch) => {
  axios
    .get("/api/user/leaderboard")
    .then(res => {
      dispatch({
        type: EACTION_TYPES.LOAD_LEADERBOARD,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};

// userin public profilinin get olunması
export const getProfile = (username:string, history:any) => (dispatch:Dispatch) => {
  axios
    .get("/api/user/u/" + username)
    .then(res => {
      dispatch({
        type: EACTION_TYPES.GET_PROFILE,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
      history.push("/");
    });
};

interface IReq{
  data: string
type: string
}
// Profilin biosunu deyismek
export const changeDescription = (req:IReq) => (dispatch:Dispatch) => {

  axios
    .post("/api/user/profile/" + req.type, { data: req.data })
    .then(res => {
      dispatch({
        type: EACTION_TYPES.GET_PROFILE,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};

export const deleteUser = (history:any) => (dispatch:Dispatch) => {
  axios
    .delete("/api/user/delete")
    .then(res => {
      logoutUser()(dispatch);
      dispatch({
        type: EACTION_TYPES.GET_PROFILE,
        payload: {}
      });
      history.push("/");
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};
