import axios from "axios";
import setAuthToken from "./setAuthToken";
import {EACTION_TYPES } from "./types";
import jwt_decode from "jwt-decode";
import { Dispatch } from 'redux';
import getRandomPhoto from "./getRandomPhoto";

interface IUserRegister {
  username: string;
  email: string;
  gender:string;
  image:string;
  password: string;
  password2: string;
} 

// User Qeydiyyat
export const registerUser = (userData:IUserRegister, history:any) => (dispatch:Dispatch) => {
  getRandomPhoto(userData.gender).then((res)=>{
    userData.image = res;
    axios
    .post("/api/user/register", userData)
    .then(res => {
      history.push("/login");
      dispatch({
        type: EACTION_TYPES.CLEAR_ERRORS
      });
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
  });

};

interface IUserLogin{
  email:string;
  password:string;
}
// Login User
export const loginUser = (userData:IUserLogin, history:any) => (dispatch:Dispatch) => {
  axios
    .post("/api/user/login", userData)
    .then(res => {
      // Local storage-da saxlamaq
      const { token } = res.data;
      localStorage.setItem("jwtToken", token);
      // tokeni auth headere set etmek
      setAuthToken(token);
      // tokeni decode etmek
      const decoded:IDecoded = jwt_decode(token);
      // hal hazırki useri set etmek
      dispatch(setCurrentUser(decoded));
      dispatch({
        type: EACTION_TYPES.CLEAR_ERRORS
      });
      history.push("/");
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};
interface IDecoded{
  exp: number;
iat: number;
id: string;
username: string;
}
export const setCurrentUser = (decoded:IDecoded|{}) => {

  return {
    type: EACTION_TYPES.SET_CURRENT_USER,
    payload: decoded
  };
};


export const logoutUser = (logoutErr = "") => (dispatch:Dispatch) => {

  localStorage.removeItem("jwtToken");

  setAuthToken(false);
  dispatch(setCurrentUser({}));
  if (logoutErr === "expired token") {
    const errors = {
      expired: "Sessiya başa çatıb. Xahiş edirik yenidən daxil olun."
    };
    dispatch({
      type: EACTION_TYPES.GET_ERRORS,
      payload: errors
    });
  }
};
