import React, { useState } from "react";
import AnimateHeight from "react-animate-height";
import { useDispatch, useSelector } from "react-redux";
import { closeVideo } from "../../actions/videoActions";
import ReactPlayer from 'react-player/lazy';
import 'react-h5-audio-player/lib/styles.css';
import "./VideoPlayer.css";


import SecondIcon from "../common/SecondIcon";
import IState from "../../interfaces";

const HEIGHT_CLOSED = 56;
const HEIGHT_OPEN = 350;

const VideoPlayer = () => {
  const dispatch = useDispatch();
  const { nowPlaying } = useSelector((state: IState) => state.video)
  const [height, setHeight] = useState(HEIGHT_CLOSED);

  const handleToggleView = React.useCallback(() => {
    closeVideo()(dispatch);
  }, [dispatch]);

  const handleOnEnded = React.useCallback(() => {
    closeVideo()(dispatch);;
  }, [dispatch])

  React.useEffect(() => {
    if (Object.keys(nowPlaying).length) {
      setHeight(HEIGHT_OPEN);
    }
    if (!Object.keys(nowPlaying).length) {
      setHeight(HEIGHT_CLOSED);
    }
  }, [nowPlaying])

  return (
    nowPlaying && (
      <>
        <AnimateHeight duration={300} height={height}>
          <div className="player-container">
            <div className="wrapper">
              <div className="main-div main-div3">
                <div className="div-h1">

                  <ReactPlayer
                    width={400}
                    height={226}
                    controls={true}
                    url={`https://www.youtube.com/embed/${nowPlaying.videoId}?rel=0&disablekb=1&enablejsapi=1&origin=http://localhost:3000`}
                    playing={true}
                    onEnded={handleOnEnded}
                  />
                   <div id="video-player-icons">
              <div>
                <i
                  className="fas fa-stop-circle player-cross"
                  onClick={handleToggleView}
                />
              </div>
              <div>
                <SecondIcon video={nowPlaying} />
              </div>  
            </div>
            
                </div>
              </div> 
            </div>

           
          </div>
        </AnimateHeight>
      </>
    )
  );
}
export default VideoPlayer;
