import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import jwt_decode from "jwt-decode";
import setAuthToken from "./actions/setAuthToken";
import { setCurrentUser, logoutUser } from "./actions/authActions";
import Navbar from "./components/layout/Navbar";
import Footer from "./components/layout/Footer";
import VideoCollection from "./components/feed/VideoCollection";
import Leaderboard from "./components/leaderboard/Leaderboard";
import Login from "./components/auth/Login";
import Register from "./components/auth/Register";
import VideoPlayer from "./components/feed/VideoPlayer";
import PublicProfile from "./components/profile/PublicProfile";
import swal from "sweetalert";
import Chat from "./components/Chat/Chat";
import "./App.css"
import IState from "./interfaces";
import { MyLikes } from "./components/MyLikesVideos/MyLikes";
import { NotFound } from "./components/NotFound/NotFound";
const App = () => {
  const dispatch = useDispatch();
  const { auth } = useSelector((state: IState) => state);

  //Tokeni yoxla keçərli deyilsə logout elə
  const verifyToken = React.useCallback(() => {
    if (localStorage.jwtToken) {
      setAuthToken(localStorage.jwtToken);
      const decoded: any = jwt_decode(localStorage.jwtToken);
      dispatch(setCurrentUser(decoded));
      const currentTime = Date.now() / 1000;
      if (decoded.exp < currentTime) {
        dispatch(logoutUser("expired token"));
      }
    }
  }, [dispatch]);
  const userIsOnline = React.useCallback(() => {
    if (!navigator.onLine) {
      swal("", "İnternet bağlantısı kəsildi.", "error");
    }
  }, []);

  React.useEffect(() => {
    verifyToken();
    setInterval(verifyToken, 60000);
    setInterval(userIsOnline, 10000);
  }, [verifyToken, userIsOnline]);

  return (
    <Router>
   
        <React.Fragment>
          <Navbar />
          <VideoPlayer />
          <Switch>
            <Route exact path="/" component={VideoCollection} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/register" component={Register} />
            <Route exact path="/leaderboard" component={Leaderboard} />
            <Route exact path="/u/:username" component={PublicProfile} />
            {/* <Route  component={NotFound} /> */}
            {/* <ProtectedRoute exact path="/chat" component={Chat} /> */}
            {auth.isAuthenticated && (      
              <Switch>
              <Route exact path="/chat" component={Chat} />
              <Route exact path="/mylikes" component={MyLikes} />
              <Route  component={NotFound} />
              </Switch>

            )}
          </Switch>
          <Footer />
          
        </React.Fragment>
     
    </Router>
  );
}
export default App;