import axios from "axios";
import {EACTION_TYPES} from "./types";
import {Dispatch} from 'redux';
import swal from 'sweetalert';
import { IUpload } from "../interfaces";

interface IVideo{
  channel: string;
date: string;
likes: [];
thumbnail: string;
title: string;
user:{ 
  username: string;
  _id: string;
};
videoId: string;
_id: string
}

export const addVideo = (videoUrl:{}) => (dispatch:Dispatch) => {
  axios
    .post("/api/video/add", videoUrl)
    .then(res => {
      loadVideos()(dispatch);
      dispatch({
        type: EACTION_TYPES.CLEAR_ERRORS
      });
      swal("", "Videonuz siyahıya əlavə olundu", "success");
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};

export const likeVideo = (video:IVideo, nowPlaying:any) => (dispatch:Dispatch) => {

  axios
    .post("/api/video/like/" + video.videoId)
    .then(res => {
      if (video.videoId === nowPlaying.videoId) {
        dispatch({
          type: EACTION_TYPES.UPDATE_NOW_PLAYING,
          payload: res.data
        });
      }
      loadVideos()(dispatch);
    })
    .catch(err => {
      
      if(err.response.data === "Unauthorized"){
        swal("","Səs vermək üçün hesabınıza giriş edin.Hesabınız yoxdursa qeydiyyatdan keçin","error");
        return;
      }
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: {singleError:"Unauthorized"}
      });
    });
};


export const deleteVideo = (video:IVideo, nowPlaying:IUpload) => (dispatch:Dispatch) => {
  axios
    .delete("/api/video/delete/" + video.videoId)
    .then(res => {
      if (video.videoId === nowPlaying.videoId) {
        dispatch({
          type: EACTION_TYPES.UPDATE_NOW_PLAYING,
          payload: {}
        });
      }
      loadVideos()(dispatch);
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};


export const loadVideos = () => (dispatch:Dispatch) => {
   axios
    .get("/api/video/all")
    .then(res => {
      dispatch({
        type: EACTION_TYPES.LIST_VIDEOS,
        payload: res.data
      });
    })
    .catch(err => {
      dispatch({
        type: EACTION_TYPES.GET_ERRORS,
        payload: err.response.data
      });
    });
};

export const playVideo = (video:IVideo) => (dispatch:Dispatch) => {
  dispatch({
    type: EACTION_TYPES.PLAY_VIDEO,
    payload: video
  });
};

export const closeVideo = () => (dispatch:Dispatch) => {
  dispatch({
    type: EACTION_TYPES.CLOSE_VIDEO
  });
};
