import { IAuthUser, IError, ILeaderboard, IUpload, IUserProfile } from "../interfaces";

export enum EACTION_TYPES {
    GET_ERRORS = "GET_ERRORS",
    CLEAR_ERRORS = "CLEAR_ERRORS",
    LIST_VIDEOS = "LIST_VIDEOS",
    SET_CURRENT_USER = "SET_CURRENT_USER",
    PLAY_VIDEO = "PLAY_VIDEO",
    CLOSE_VIDEO = "CLOSE_VIDEO",
    UPDATE_NOW_PLAYING = "UPDATE_NOW_PLAYING",
    LOAD_LEADERBOARD = "LOAD_LEADERBOARD",
    GET_PROFILE = "GET_PROFILE",
    DELETE_VIDEO = "DELETE_VIDEO",
}

//authReducer actions
export interface IAuthAction {
    type: EACTION_TYPES.SET_CURRENT_USER,
    payload: IAuthUser
}
//errorReducer actions
export interface IGETErrorAction {
    type: EACTION_TYPES.GET_ERRORS,
    payload: IError
}
export interface ICLEARErrorAction {
    type: EACTION_TYPES.CLEAR_ERRORS,
    payload: {}
}
export type IErrorAction = IGETErrorAction | ICLEARErrorAction;

//userReducer actions
interface IUserLoadLeaderboardAction {
    type: EACTION_TYPES.LOAD_LEADERBOARD,
    payload: ILeaderboard
}
interface IUserGetProfileAction {
    type: EACTION_TYPES.GET_PROFILE,
    payload: IUserProfile
}

export type IUserAction = IUserLoadLeaderboardAction | IUserGetProfileAction;


//videoReducer actions
interface IVideoListVideosAction {
    type: EACTION_TYPES.LIST_VIDEOS
    payload:IUpload[]
}
interface IVideoPlayVideoAction {
    type: EACTION_TYPES.PLAY_VIDEO
    payload:IUpload
}
interface IVideoCloseVideoAction {
    type: EACTION_TYPES.CLOSE_VIDEO
}
interface IVideoUpdateNowPlayingAction {
    type: EACTION_TYPES.UPDATE_NOW_PLAYING
    payload:IUpload
}

export type IVideoAction = IVideoListVideosAction | IVideoPlayVideoAction | IVideoCloseVideoAction | IVideoUpdateNowPlayingAction