import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { popErrors } from "../../actions/errorActions";
import IState from "../../interfaces";
import "./ErrorPopup.css";

const ErrorPopup =()=> {
  const dispatch = useDispatch();
  const store = useSelector((state:IState) => state)
  const [visible, setVisible] = useState(false);

  React.useEffect(()=>{
    setVisible(prev=>(!prev));
  },[store.errors])


  const handleOnClick = () => {
    setVisible(false);
    dispatch(popErrors());
  };
    let errors = {...store.errors};
    let classes = visible ? "" : "hide ";
    classes += "error-popup alert alert-dismissible fade show";
    if (errors.singleError) {
      errors.singleError === "Unauthorized"
        ? (errors.singleError = "Bu xüsusiyyəti istifadə etmək üçün hesabınıza daxil olun.")
        : (errors.singleError = "Server xətası, xahiş olunur daha sonra yenidən cəhd edin.")
    }
   
    return (
      <React.Fragment>
        {Object.keys(errors).length ? (
          <div className={classes} role="alert">
            <i className="fas fa-exclamation-triangle" />
            &nbsp; Upsss, xəta baş verdi!
            <ul>
              {Object.values(errors).map((err, index) => (
                <li key={index}> {err}</li>
              ))}
            </ul>
            <button
              type="button"
              className="error-popup-cross"
              onClick={handleOnClick}
            >
              &nbsp; &times;
            </button>
          </div>
        ) : (
            ""
          )}
      </React.Fragment>
    );
  
}

export default ErrorPopup;