import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { loadVideos } from '../../actions/videoActions';
import IState, { IUpload } from '../../interfaces';
import Video from '../common/Video';

export const MyLikes = () => {
    const dispatch = useDispatch();
  const { video:{videoList},auth } = useSelector((state: IState) => state);
    const myLikesVideo = videoList.filter(video=> video.likes.includes(auth.user.id));
  React.useEffect(() => {
    loadVideos()(dispatch);
  }, [dispatch])

  return (
    <>
    <h2 className="text-primary m-3"> Bəyəndiklərim</h2>
      <div className="feed">
        
        {myLikesVideo.map((video: IUpload, index: number) => (
                <Video video={video} key={index} />
              ))}
      </div>


      
    </>
  );
}
